.Dashboard {
    text-align: center;
}

.Dashboard-link {
    color: #61dafb;
}

.header-white {
    background-color: white;
}

.fixed-height {
    max-height: 18em;
    overflow-y: scroll !important;
}

.left-col {
    flex: 1;
    margin-right: 0.5em;
}

.right-col {
    flex: 1;
    margin-left: 0.5em;
}

.flex-row {
    display: flex;
    margin-top: 1em;
}

.top-space {
    margin-top: 1em;
}

.flex-column {
    display: flex;
    flex-direction: 'column';
}