.Dashboard {
    text-align: center;
}

.Dashboard-link {
    color: #61dafb;
}

.header-white {
    background-color: white;
}

.fixed-height {
    max-height: 18em;
    overflow-y: scroll !important;
}

.left-col {
    -webkit-flex: 1 1;
            flex: 1 1;
    margin-right: 0.5em;
}

.right-col {
    -webkit-flex: 1 1;
            flex: 1 1;
    margin-left: 0.5em;
}

.flex-row {
    display: -webkit-flex;
    display: flex;
    margin-top: 1em;
}

.top-space {
    margin-top: 1em;
}

.flex-column {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: 'column';
            flex-direction: 'column';
}
#modelViewer {
    margin-top: 1em;
    margin-bottom: 1em;
    display: inline-block;
}
.search-inputs {
    -webkit-flex-direction: column;
            flex-direction: column;
    margin-top: 1em;
    margin-bottom: 1em;
}
.search-inputs button {
   margin-left: 1em;
}

.poly-thumbnail {
    height: 100px;
    width: 100px;
    min-height: 100px;
    min-width: 100px;
}
.thumbNailView img {
  height: 50px;
  width: 75px;
}
